import { Component, ViewChild, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { ProductService } from "../../providers/product.services";
import { environment } from '../../../environments/environment';

@Component({
  selector: "page-EVP",
  templateUrl: "EVP.html",
  styleUrls: ["./EVP.scss"],
})
export class EVPPage implements OnInit {
  public productList: any;
  public searchWord: any;
  public baseUrl = environment.baseUrl + "/";
  constructor(private _ProductService: ProductService) {}

  ngOnInit() {
    this.getProductList();
  }

  public showSearchBox: Boolean = false;
  enableSearch() {
    this.showSearchBox = true;
  }

  backToNormal() {
    this.showSearchBox = false;
  }

  getProductList() {
    this._ProductService.getProductList(localStorage.getItem('shopID')).subscribe((data: any) => {
      this.productList = data;
      console.log(this.productList);
    });
  }

  public isSearchEnable: Boolean = false;

  public searchproductList: any = [];
  search(query) {
    if (query.target.value.length >= 2) {
      this.isSearchEnable = true;
      this._ProductService
        .search({
          search: query.target.value,
          filter: "Product",
        },localStorage.getItem('shopID'))
        .subscribe((data: any) => {
          console.log(data, "data");
          this.searchproductList = data.productList;
        });
    }

    if (query.target.value.length == 0) {
      this.isSearchEnable = false;
      // this.getAnalyticalData()
    }
  }
}
